*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Muli', sans-serif;
  background-image: url(/static/img/overlay1.png), url(/static/img/overlay2.svg), -webkit-linear-gradient(#e37682, #5f4d93);
  background-image: url(/static/img/overlay1.png), url(/static/img/overlay2.svg), linear-gradient(#e37682, #5f4d93);
  background-color: #e37682;
  background-attachment: fixed;
  background-position: top left, center center, center center;
  background-size: auto, cover, cover;
  color: #a58fe9;
  overflow-y: scroll;
}

@media all and (max-width: 980px) {
  body {
    background-image: url(/static/img/overlay2.svg);
  }
}

html::-webkit-scrollbar {
  -webkit-appearance: none;
          appearance: none;
  width: 10px;
}

html::-webkit-scrollbar-track {
  -webkit-appearance: none;
          appearance: none;
  width: 10px;
  background-color: #ffffff;
}

html::-webkit-scrollbar-thumb {
  -webkit-appearance: none;
          appearance: none;
  width: 10px;
  background-color: #ff6272;
}

html::-webkit-scrollbar-thumb:hover {
  -webkit-appearance: none;
          appearance: none;
  width: 10px;
  background-color: #ff5566;
}

a {
  text-decoration: none;
  color: #ff5566;
}
.App {
  overflow: scroll;
}

.row {
  margin: 0;
}

.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 0;
}

.sub-title {
  color: white;
  font-family: 'Lobster', cursive !important;
  font-weight: 100 !important;
  font-size: 30px;
  text-align: center
}

.title-grad {
  font-size: 36px;
  background: -webkit-linear-gradient(45deg, #a58fe9, #ff5566);
  background: linear-gradient(45deg, #a58fe9, #ff5566);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.page-title {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  font-size: 36px;
  position: relative;
  margin: 100px auto 50px auto;
}

.page-title:after {
  position: absolute;
  content: ' ';
  top: 50%;
  left: 100%;
  margin: 0 10px;
  width: 150px;
  height: 5px;
  background-color: white;
}

.page-title:before {
  position: absolute;
  content: ' ';
  top: 50%;
  right: 100%;
  margin: 0 10px;
  width: 150px;
  height: 5px;
  background-color: white;
}

.tags {
  pointer-events: none;
  padding: 0px 10px;
  margin: 5px 10px 5px 0px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  background-color: transparent;
  font-size: 12px;
}

.bg-wave {
  position: absolute;
  width: 100%;
  height: 100%;
  top: -30%;
  left: 0px;
  fill: white;
  opacity: 0.1
}

.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

@media all and (max-width: 900px) {
  .bg-wave {
    display: none;
  }
}
